@import url(https://fonts.googleapis.com/css2?family=Asap+Condensed:ital,wght@1,700&family=Roboto&family=Sanchez&family=Satisfy&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.recipe-card {
  align-items: center;
  background: linear-gradient(to top left, #ff7b00, #fda65d);
  border-radius: 15px;
  box-shadow: 8px 15px 20px rgba(0, 0, 0, 0.2);
  display: flex;
  flex-direction: column-reverse;
  margin-bottom: 50px;
  margin-top: 160px;
  max-height: 150px;
  max-width: 280px;
  min-height: 125px;
  padding: 2px 18px;
  transition: all ease 0.6s;
}

.recipe-card:hover {
  background: linear-gradient(to top left, #fda65d, #ff7b00, #ff7b00);
  box-shadow: 8px 15px 20px rgba(0, 0, 0, 0.4);
  transform: translateY(-20px);
}

.recipe-image {
  border-radius: 250px;
  height: 240px;
  width: 240px;
}

.recipe-title {
  color: black;
  font-family: Sanchez , serif;
  font-size: 28px;
  font-weight: 700;
  text-align: center;
}

a:hover {
  text-decoration: none;
}

iframe {
  align-self: center;
  width: 90vw;
}

.details-container {
  background: linear-gradient(to top, #e26a2c, #27282d,
  #27282d, #27282d, #27282d, #27282d, #27282d, #27282d, #27282d, #27282d);
  color: white;
  display: flex;
  flex-direction: column;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

.details-infos {
  align-items: center;
  display: flex;
  flex-direction: column;
  font-family: Sanchez , serif;
  font-size: 4.5vw;
}

.details-recipe-title {
  border-bottom: 2px solid #e26a2c;
  font-family: Sanchez , serif;
  font-size: 7vw;
  margin: 0 auto;
  margin-bottom: 15px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.details-alcoholic {
  font-family: Sanchez , serif;
  font-size: 5vw;
  text-align: center;
}

.recipe-category {
  border-bottom: 12px dotted white;
  padding: 3px 0;
  margin-bottom: 25px;
}

.in-progress-category {
  font-family: Sanchez , serif;
  font-size: 5.5vw;
  text-align: center;
}

.instructions {
  width: 90vw;
}


.details-start-recipe {
  align-self: center;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
  bottom: 0;
  margin: 0;
  position: fixed;
  width: 100vw;
}

.details-image {
  align-self: center;
  border-radius: 20px;
  height: 35vh;
  margin: 20px;
  width: 80vw;
}

button {
  background-color: #ff7b00;
  border: none;
  border-radius: 18px;
  margin: 5px 2.5px;
  outline: none;
  padding: 10px;
  transition: all ease 0.5s;
}

button:hover {
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.8);
  outline: none;
}

.header-container {
  align-items: center;
  display: flex;
  justify-content: space-between;
  margin-bottom: 25px;
  width: 100%;
}

.header-container-expanded {
  display: flex;
  flex-direction: column;
  text-align: center;
  transition: all ease 0.5s;
  width: 100%;
}

.header-explore {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  width: 100%;
}

.page-title {
  color: white;
  font-family: Sanchez , serif;
  font-size: 20px;
  font-weight: 800;
  margin-top: 10px;
}

.search-input {
  border: 1px solid grey;
  border-radius: 18px;
  height: 50px;
  margin-top: 7px;
  width: 97%;
}

.search-input:hover {
  border: 2px solid #e26a2c;
  border-radius: 18px;
  outline: none;
}

.filter-options {
  color: white;
  display: flex;
  font-family: Sanchez , serif;
  font-size: 4.5vw;
  justify-content: space-around;
  margin: 10px 0;
  width: 100%;
}

.filter-options label {
  align-items: center;
  display: flex;
  justify-content: space-between;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.filter-options input {
  margin-right: 5px;
}

.search-button {
  background-color: #e26a2c;
  border-radius: 8px;
  font-size: 18px;
  font-weight: 700;
  width: 100%;
}

.footer-menu {
  align-items: center;
  background-color: #e26a2c;
  bottom: 0;
  display: flex;
  justify-content: space-around;
  padding: 2px;
  position: fixed;
  left: 0;
  right: 0;
}

.drinks-recipes-section {
  background: linear-gradient(to top, #e26a2c, #27282d,
  #27282d, #27282d, #27282d, #27282d, #27282d, #27282d, #27282d, #27282d);
}

.drinks-recipes-container {
  align-items: flex-start;
  background-image: url(/recipes-app/static/media/background.e23a70d3.png);
  background-repeat: no-repeat;
  background-size: 75vh;
  display: flex;
  flex-wrap: wrap;
  height: 100vh;
  justify-content: center;
  overflow-y: auto;
}

.header-and-categories-container {
  display: flex;
  flex-direction: column;
  padding: 5px;
  width: 100vw;
}

.explore-section, .explore-food-section, .explore-ingredients-section {
  background: linear-gradient(to top, #e26a2c, #27282d,
  #27282d, #27282d, #27282d, #27282d, #27282d, #27282d, #27282d, #27282d);
  height: 100vh;
  overflow-y: auto;
}

.explore-wrapper {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.explore-btn-container {
  display: flex;
  flex-wrap: wrap;
  font-family: Sanchez , serif;
  justify-content: center;
  margin-bottom: 20px;
}

.loading-gif {
  display: flex;
  margin: auto;
  width: 365px;
}

.ingredient-card {
  display: flex;
  flex-direction: column;
}

.loading-container {
  align-items: center;
  background-color: #e97723;
  display: flex;
  height: 100vh;
}

.loading-gif {
  border-radius: 100vh;
  display: flex;
  width: 45vh;
}

.food-recipes-section {
  background: linear-gradient(to top, #e26a2c, #27282d,
  #27282d, #27282d, #27282d, #27282d, #27282d, #27282d, #27282d, #27282d);
}

.food-recipes-container {
  align-items: flex-start;
  background-image: url(/recipes-app/static/media/background.e23a70d3.png);
  background-repeat: no-repeat;
  background-size: 75vh;
  display: flex;
  flex-wrap: wrap;
  height: 100vh;
  justify-content: center;
  overflow-y: auto;
}

.categories-container {
  display: flex;
  flex-wrap: wrap;
  font-family: Sanchez , serif;
  justify-content: center;
}

.categories-container button {
  background-color: #fda65d;
  margin-left: 10px;
}

.categories-container button:hover {
  background-color: #ff7b00;
}


.recipes-container {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-evenly;
  overflow: auto;
}

.login-page {
  background-image: url(/recipes-app/static/media/raining.9789779f.gif);
  display: flex;
  flex-direction: column;
  height: 100vh;
  justify-content: center;
}

#nomad-recipes {
  width: 70vw;
  margin: 0 auto;
  max-width: 600px;
}

.login-container {
  background-color: #f78812;
  border-radius: 15px;
  box-shadow: 1px 2px 10px rgba(0, 0, 0, 0.7);
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 0 auto;
  width: 60vw;
  padding: 20px;
}

.login-container input {
  border: 1px solid gray;
  border-radius: 10px;
  font-size: 18px;
  font-weight: 800;
  height: 9vw;
  max-height: 50px;
  margin: 7px 0;
  outline: none;
  text-align: center;
  transition: all ease 0.1s;
}

.login-container input:hover {
  border: 2px solid #468233;
}

.login-container button {
  background-color: #d65210;
  box-shadow: 2px 5px 10px rgba(0, 0, 0, 0.9);
  font-family: Sanchez , serif;
  font-weight: 700;
  height: 8vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.perfil-section {
  background: linear-gradient(to top, #e26a2c, #27282d,
  #27282d, #27282d, #27282d, #27282d, #27282d, #27282d, #27282d, #27282d);
  color: white;
  height: 100vh;
}

.perfil-container {
  align-items: center;
  display: flex;
  flex-flow: column wrap;
  justify-content: center;
}

.user-email {
  font-size: 6.5vw;
}

.strikethrough {
  text-decoration: line-through;
}

.recipe-made-card {
  align-items: center;
  background: linear-gradient(to top left, #ff7b00, #fda65d);
  border-radius: 10px;
  box-shadow: 1px 1px 16px black;
  cursor: pointer;
  display: flex;
  flex-flow: row wrap;
  font-family: Sanchez , serif;
  font-size: 18px;
  justify-content: center;
  margin: 3.2vh 7vw;
  transition: all ease 0.4s;
}

.recipe-made-card a {
  color: black;
}

.recipe-made-card:hover {
  background: linear-gradient(to top left, #fda65d, #ff7b00, #ff7b00);
  box-shadow: 8px 15px 20px rgba(0, 0, 0, 0.4);
  transform: translateY(-20px);
}

.recipe-made-tags-and-image {
  display: flex;
  flex-flow: row wrap;
  font-weight: 600;
  justify-content: center;
  margin-bottom: 5px;
}


.recipe-made-image {
  border-radius: 12px;
  box-shadow: 5px 5px 20px black;
  width: 250px;
}


.recipe-made-infos {
  align-items: center;
  color: black;
  display: flex;
  flex-flow: column wrap;
  justify-content: space-around;
  width: 95vw;
}

@media screen and ( min-width : 445px ) and ( max-width : 700px ) {

  .recipe-made-card {
    justify-content: space-between;
  }

  .recipe-made-image {
    margin: 0 25px;
  }

  .recipe-made-tags-and-image {
    align-items: center;
    flex-direction: row-reverse;
    margin: 10px 0;
  }

  .recipe-made-infos {
    width: 82vw;
  }

  .recipes-made-buttons {
    display: flex;
    justify-content: center;
    width: 82vw;
  }
}

@media screen and ( min-width : 700px ) {

  .recipe-made-card {
    margin: 5vh auto;
    width: 90vw;
  }

  .recipe-made-tags-and-image {
    align-items: center;
    display: flex;
    flex-direction: row-reverse;
    height: 41vh;
    margin: 0;
    margin-left: 16px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
  }

  .recipe-made-image {
    margin: 0;
    margin-right: 10px;
  }

  .recipe-made-infos-container {
    display: flex;
    width: 89vw;
  }

  .recipe-made-infos {
    align-self: center;
    flex-direction: row;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
  }

}


.not-found-container {
  display: flex;
  flex-direction: column;
  height: 100vh;
}

.not-found-title {
  font-size: calc(100% + 4.8vmin);
  margin: auto;
  text-align: center;
}

.not-found-subtitle {
  font-size: calc(60% + 0.8vmin);
  margin-top: -40px;
  text-align: center;
}

.not-found-gif {
  margin: auto;
  width: 300px;
}

